/* src/components/Navbar.css */

.navbar.bg-custom {
    background-color: #9bcaf2; /* Cambia este color al deseado */
  }
  
  .navbar .navbar-brand {
    color: #ffffff; /* Color del texto de la marca */
  }
  
  .navbar .nav-link {
    color: #ffffff; /* Color de los enlaces de navegación */
  }
  
  .navbar .dropdown-menu {
    background-color: #9bcaf2; /* Color de fondo del menú desplegable */
  }
  
  .navbar .dropdown-item {
    color: #ffffff; /* Color de los elementos del menú desplegable */
  }
  
  .navbar .dropdown-item:hover {
    background-color: #87b5e0; /* Color de fondo cuando se pasa el mouse sobre un elemento */
    color: #ffffff; /* Color del texto cuando se pasa el mouse sobre un elemento */
  }
  